import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as ui_actions from 'src/redux/actions/ui';
import * as word_actions from 'src/redux/actions/word';
import * as domain_actions from 'src/redux/actions/domain';
import { Button, Input } from 'antd';
import { Styled } from './Search.styled';
import SelectTld from 'src/components/Search/SelectTld';
import WordPoss from "../WordPoss";

/*
 * NOTES:
 * 0) page loaded ... parse url
 * 1) url changed ... will any other file be changing the url? if so, it will not contain any variables
 * 2) user changes word_input ... update state, change url
 * 3) user changes tld ... update state, change url
 */
let tldValue = function (props) {
  return Object.keys(props.tlds_user)[0] || Object.keys(props.tlds_checked)[0] || '';
};

class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      word_input: props.word_input || '',
      tld: tldValue(props)
    };
  }

  componentDidUpdate(prevProps) {
    /*
     * when tlds_user or tlds_checked changes,
     * apply first in list as value of SelectTld
     */
    if (
      Object.keys(this.props.tlds_checked).length !== Object.keys(prevProps.tlds_checked || {}).length ||
      Object.keys(this.props.tlds_user).length !== Object.keys(prevProps.tlds_user || {}).length
    ) {
      let tld = tldValue(this.props);
      this.setState({
        tld
      });
      this.set_tld(tld);
    }
  }

  componentDidMount() {
    /*
     * read url for word_input
     */
    let word_input = this.props.match.params.key;
    this.set_input(word_input);
  }

  set_input = word_input => {
    // if url contains ".", parse TLD from input string
    let doti = word_input.indexOf('.');
    if (doti !== -1) {
      let tld = word_input.substr(doti + 1);
      // set tld (local and redux)
      if (tld.trim()) {
        this.set_tld(tld);
      }
      // fix input
      word_input = word_input.substr(0, doti);
    }
    // set input (local and redux)
    this.setState({ word_input: word_input }, this.set_url);
    this.props.word_actions.RX__word_chunks(word_input);
  };

  set_tld = tld => {
    // set state (local and redux)
    this.setState({ tld: tld }, this.set_url);
    this.props.domain_actions.RX__tld_add(tld, 'user');
  };

  set_url = () => {
    /*
     * set url
     * (/{page_name}/{word_input}.{tld})
     */
    let pathlist = this.props.location.pathname.split('/');
    pathlist[2] = this.state.word_input;
    let newpath = pathlist.join('/');
    if (!!this.props.domains) {
      newpath += '.' + this.state.tld;
    }
    if (newpath !== this.props.location.pathname) {
      this.props.history.push(newpath);
    }
    /*
     * set title
     */
    window.document.title = this.state.word_input; // + (this.state.tld ? ' .' + this.state.tld : '');
  };

  set_state = (key, value) => {
    this.setState({
      [key]: value
    });
  };

  render() {
    let { title, cue, hideInput, domains, className, ...props } = this.props;
    return (
      <Styled className={"Search " + className}>
        <div className="content">
          {/*
           * Title
           * If mobile, show only last line (last item in array)!
           */}
          {!!title && (
            <h1 className="title">
            {title}
          </h1>
          )}

          {/*
           * Input Group
           */}
          <div className={'input-group'}>
            {!hideInput && (
              <Input
                className="Input"
                placeholder={'enter a few words or a phrase...'}
                value={this.state.word_input}
                onChange={event => {
                  this.set_state('word_input', event.target.value);
                }}
                onKeyPress={event => {
                  if (event.key === 'Enter') {
                    this.set_input(this.state.word_input);
                  }
                }}
              />
            )}
            {!!domains && (
              <SelectTld
                value={this.state.tld}
                placeholder=""
                onSelect={value => {
                  this.set_tld(value);
                }}
              />
            )}
            <Button
              className="Button"
              onClick={() => {
                this.set_input(this.state.word_input);
              }}
            >
              <span className="hide-small">Search &thinsp; </span>
              <i className="fas fa-search"></i>
            </Button>
          </div>

          {/*
           * Cue message
           */}
          {!!cue && (
            <div className="cue hide-small">
              <div className="p">{cue}</div>
            </div>
          )}

          {/*
           * Open POSS
           */}
          <div
            className="show_poss"
            onClick={() => {
              props.ui_actions.RX__toggle_poss();
            }}
          >
            <span>...</span>
          </div>
        </div>

        {/*
         * POSS content, below Search form
         */}
        <WordPoss {...props} />
      </Styled>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    domain_actions: bindActionCreators(domain_actions, dispatch),
    word_actions: bindActionCreators(word_actions, dispatch),
    ui_actions: bindActionCreators(ui_actions, dispatch)
  };
};

const mapStateToProps = function (state) {
  return {
    word_input: state.word.word_input,
    tlds_user: state.domain.tlds_user,
    tlds_checked: state.domain.tlds_checked
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Search);
