import * as sh from "universal-common-scripts/functions/sh";
import { asort_by_rating } from "universal-common-scripts/functions/asort_strings";

/**
 * Domain extensions suggestions
 * @param chunks_keys {array} - array of key strings
 * @param chunks_dict {object} - object of key : {key:'',root:'',derivations:[],ok_list:[],...etc}
 * @params array {array} - list of words, to append new suggestions to
 * @returns array {array} - flat list of domain extensions which match inputted chunks
 *      the domain extensions come from the chunks themselves, each chunk row.poss.domains
 */
export default function (chunks_keys, chunks_dict) {
	// output (will convert this Set to Array)
	let best_domains = new Set();
	let all_domains = new Set();

	/*
	 * Fulltext, to search later
	 */
	let fulltext_poss = '';
	let fulltext_domains = '';
	let fulltext_all_domains = '';

	/*
	 * Sort chunks - make new list of row-data/domains objects
	 * rows with fewer domains first
	 */
	let drows = [];
	let maxLength = 0;
	let maxWidth = 0;
	for (let key of chunks_keys) {
		// word data, including tlds
		let row = chunks_dict[key];
		if (row.poss && row.poss.domains) {
			/*
			 * Each `drows` item contains best domains and pos info
			 */
			// parse domain/row data
			let doms = [...row.poss.domains];
			let drow = {
				pos1: row.pos1,
				doms: doms,
			};
			// compare ("width" / character length)
			// instead of just array length, to account for length of words
			let thiswidth = JSON.stringify(doms).length;
			if (thiswidth > maxWidth) {
				// if longer, push to end
				drows.push(drow);
				maxWidth = thiswidth;
				maxLength = doms.length; // remember length of array
			} else {
				// if shorter, put to beginning
				drows.unshift(drow);
			}
			/*
			 * Extra: compile content to search/sort for later
			 */
			// save all domains
			for (let tld of row.poss.all_domains) {
				all_domains.add(tld);
			}
			// fulltext search
			// of all poss content, except domains (include all_domains)
			let until_domains = JSON.stringify(row.poss);
			fulltext_poss += until_domains.substr(0, until_domains.indexOf('"domains":'));
			// fulltext search
			// of only domains or all_domains
			fulltext_domains += (row.poss.domains ? JSON.stringify(row.poss.domains) : '');
			fulltext_all_domains += (row.poss.all_domains ? JSON.stringify(row.poss.all_domains) : '');
		}
	}

	/*
	 * All lists of domains should be same length
	 * So, interpolate shorter lists with empty values
	 */
	for (let drow of drows) {
		let thisDoms = drow.doms.length;
		if (thisDoms < maxLength) {
			// how many are missing?
			let delta = maxLength - thisDoms;
			// how many spaces should we insert between each current dom?
			let insert_n = Math.floor(delta / (thisDoms - 1));
			// ok, insert the spaces
			for (let i = thisDoms - 1; i >= 1; i--) {
				drow.doms.splice(i, 0, ...new Array(insert_n));
			}
		}
	}

	/*
	 * Assemble final list of domains
	 */
	// count to 10
	for (let i = 0; i < maxLength; i++) {
		// look at each chunk
		// will add ONE tld from each chunk
		for (let drow of drows) {
			// skip helper morphemes
			if (['pronouns', 'prepositions', 'determiners', 'interjections', 'conjunctions'].includes(drow.pos1)) {
				continue;
			}
			// get ${i} tld (domain extension) of chunk
			if (drow.doms[i]) {
				best_domains.add(drow.doms[i]);
			}
		}
	}

	/*
	 * Sort list by frequency (if occurs more than once, push it higher)
	 */
	// each extra from all_domains
	for (let tld of all_domains) {
		// match fulltext_all_domains
		let re = new RegExp('"' + tld + '"', 'g');
		let matches = fulltext_poss.match(re);
		if (matches && matches.length >= 2) {
			// if appears 2+ times, add to best list
			best_domains.add(tld);
		}
	}
	// convert to array
	let arr = [...best_domains];
	// count frequency
	let frequency = {};
	for (let tld of arr) {
		// match fulltext_poss
		let re = new RegExp('"' + tld + '"', 'g');
		let matches = fulltext_poss.match(re);
		frequency[tld] = (matches ? matches.length : 0);
	}
	// sort by frequency
	arr = asort_by_rating(arr, frequency);

	/*
	 * Return
	 */
	return arr;

};
