import React from 'react';
import { connect } from 'react-redux';
import Select from 'react-select';
import { Styled } from 'src/components/Search/SelectTld/SelectTld.styled';
import * as sh from 'universal-common-scripts/functions/sh';

class SelectTld extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: undefined,
      options: [],
      autoFocus: false,
      showingAllOptions: false
    };
    this.refSelectTld = React.createRef();
  }

  componentDidMount() {
    // tld list options
    this.newOptions(25);
    // initially selected value
    if (this.props.value) {
      this.setState({
        value: this.props.value
      });
    }
  }

  componentDidUpdate(prevProps) {
    /*
     * When list of domains changed (all or checked), re-render options list
     */
    if (Object.keys(this.props.tlds_unchecked).length !== Object.keys(prevProps.tlds_unchecked).length) {
      this.newOptions(25);
    }
    /*
     * Focus, open select options (when click somewhere else, change redux state)
     */
    if (prevProps.focusSelectTld !== this.props.focusSelectTld) {
      if (this.refSelectTld.current) {
        this.refSelectTld.current.focus();
      }
    }
    /*
     * User selected value
     */
    if (prevProps.value !== this.props.value) {
      // React-Select component requires its value prop to be not a simple string value,
      // but a reference to one of the options!
      let opt = this.state.options.find(opt => opt.value === this.props.value);
      this.setState({
        selectedOption: opt
      });
    }
  }

  /**
   * re-generates this.state.Options
   * @param limit {number}
   */
  newOptions = (limit = undefined) => {
    // make <option> for each .tld in list
    let defaultSelectedOption = null;
    let selectedOption = null;
    let options = [];
    let tlds = { ...this.props.tlds_user, ...this.props.tlds_checked, ...this.props.tlds_all };
    for (let tld in tlds) {
      let opt = {
        value: tld,
        label: (
          <>
						<b>.</b>
            {tld}
					</>
        )
      };
      options.push(opt);
      if (opt.value === this.props.value) {
        selectedOption = opt;
      }
      if (opt.value === 'com') {
        defaultSelectedOption = opt;
      }
    }
    // set, and remember if showing all
    this.setState({
      options,
      autoFocus: false,
      showingAllOptions: !limit,
      selectedOption: selectedOption || defaultSelectedOption,
    });
  };
  showAllOptions = () => {
    if (!this.state.showingAllOptions) {
      setTimeout(() => {
        this.newOptions();
        // debugger; // reminder: setTimeout is the only way to debug CSS of select dropdown
      }, 200);
    }
  };
  hideAllOptions = () => {
    if (this.state.showingAllOptions) {
      setTimeout(() => {
        this.newOptions(25);
      }, 200);
    }
  };

  render() {
    return (
      <Styled className="SelectTld">
				<Select
          // classNamePrefix="DoNotUse-ThisMakesItWorse"
          openMenuOnFocus={true}
          ref={this.refSelectTld}
          value={this.state.selectedOption}
          placeholder={
            this.props.placeholder || (
              <>
								<b>.</b>com
							</>
            )
          }
          onChange={item => {
            if (this.props.onSelect) {
              this.props.onSelect(item.value);
            }
          }}
          isSearchable={true}
          options={this.state.options}
          onFocus={this.showAllOptions}
          onBlur={this.hideAllOptions}
        ></Select>
			</Styled>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {};
};

const mapStateToProps = function (state) {
  return {
    tldsChanged: state.domain.tldsChanged,
    tlds_all: state.domain.tlds_all,
    tlds_user: state.domain.tlds_user,
    tlds_checked: state.domain.tlds_checked,
    tlds_unchecked: state.domain.tlds_unchecked,
    focusSelectTld: state.ui.focusSelectTld
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SelectTld);
