import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import AdminRoutes from 'src/routes/Admin';
import SearchRoutes from 'src/routes/Search';

/*
	Routes
*/
function Routes() {
  return (
    <Switch>
			<Route
        path="/add_*"
        render={props => {
          return <AdminRoutes {...props} />;
        }}
      />
			<Route
        path="/edit_*"
        render={props => {
          return <AdminRoutes {...props} />;
        }}
      />
			<Route
        path="/domain"
        render={props => {
          return <SearchRoutes {...props} />;
        }}
      />
			<Route
        path="/domains"
        render={props => {
          return <SearchRoutes {...props} />;
        }}
      />
			<Route
        path="/domain/*"
        render={props => {
          return <SearchRoutes {...props} />;
        }}
      />
			<Route
        path="/domains/*"
        render={props => {
          return <SearchRoutes {...props} />;
        }}
      />
			<Route
        path="/thesaurus"
        render={props => {
          return <SearchRoutes {...props} />;
        }}
      />
			<Route
        path="/about"
        render={props => {
          let AboutRoutes = require('./About').default;
          return <AboutRoutes {...props} />;
        }}
      />
			<Redirect from="/" exact to="/domain/hello world"/>
			<Route
        path="/"
        render={props => {
          let ErrorRoutes = require('./Error').default;
          return <ErrorRoutes {...props} />;
        }}
      />
		</Switch>
  );
}

export default Routes;
