import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route } from 'react-router-dom';

/*
	CSS, JS
*/
import 'src/style-reset.scss';
import 'src/style-responsive.scss';
import 'antd/dist/antd.css';
import 'src/style-antd.scss';
import 'src/style-ui.scss';
import 'src/style-classes.scss';
import 'src/style-layout.scss';

/*
	Connect Redux (global state)
*/
import { Provider } from 'react-redux';
import rx_store from './redux/store';

/*
	App
*/
import LayoutAndRoutes from 'src/routes/index.js';

/*
	Routes
*/
ReactDOM.render(
  <Provider store={rx_store}>
		<Router>
			<Route component={LayoutAndRoutes}/>
		</Router>
	</Provider>,
  document.getElementById('App')
);
