/**
 * Add list of aggregated abbreviated phrases, from given list of lists of phrases
 * @param lists {object} - named list of lists of phrases
 * @param chunks_keys {array} - array of key strings
 * @param chunks_dict {object} - object of key : {key:'',root:'',derivations:[],ok_list:[],...etc}
 * @param position {number} - index in between param lists - where to insert new list
 * @returns OP.lists {array}
 */
export default function add_abbreviated_list(lists, chunks_keys, chunks_dict, position = 1) {
	let new_list = []; // list of phrase strings
	// prepare find/replace pairs
	let find_replace = []; // tuples: [key, abbreviation]
	for (let key of chunks_keys) {
		let chunk = chunks_dict[key];
		if (chunk.key && chunk.abbreviation && chunk.abbreviation.length < chunk.key) {
			find_replace.push([chunk.key, chunk.abbreviation.toLowerCase()]);
		}
		if (chunk.key && chunk.acronym && chunk.acronym.length < chunk.key) {
			find_replace.push([chunk.key, chunk.acronym.toLowerCase()]);
		}
		if (chunk.key && chunk.root && chunk.root.length < chunk.key) {
			find_replace.push([chunk.key, chunk.root.toLowerCase()]);
		}
	}
	// each list/phrase, perform each find/replace pair
	for (let list of Object.values(lists)) {
		let phrase_of_list_replaced = 0;
		phrase_of_list:
			for (let phrase of list) {
				for (let [find, replace] of find_replace) {
					// add space after word - so not to replace domain extension
					if (phrase.includes(find + ' ') && !phrase.includes(replace + ' ') && !phrase.includes('.' + find) && !phrase.includes('.' + replace)) {
						// save edited phrase to new list
						new_list.push(phrase.replace(find + ' ', replace + ' '));
						// count how many
						phrase_of_list_replaced++;
						// break if too many
						if (phrase_of_list_replaced >= 5) {
							break phrase_of_list;
						}
					}
				}
			}
	}
	// insert new list into middle of old lists
	let new_lists = {};
	let i = 0;
	for (let listname in lists) {
		let list = lists[listname];
		if (i === position && new_list.length) {
			new_lists["abbreviated"] = [...new Set([...new_list])];
		}
		new_lists[listname] = list;
		i++;
	}
	return new_lists;
};
