import React from "react";
import {Route, Switch} from 'react-router-dom';
import Layout from "src/Layout";

export default function (props) {
	return (
		<Switch>
			<Route
				key="edit_domain"
				path="/edit_domain/:key?"
				render={(props) => {
					let Component = require("../pages/Admin/EditDomain").default;
					return (
						<Layout {...props}>
							<Component {...props} />
						</Layout>
					);
				}}/>
			<Route
				key="edit_word"
				path="/edit_word/:key?"
				render={(props) => {
					let Component = require("../pages/Admin/EditWord").default;
					return (
						<Layout {...props}>
							<Component {...props} />
						</Layout>
					);
				}}/>
			<Route
				key="add_word_to_others"
				path="/add_word_to_others/:key?"
				render={(props) => {
					let Component = require("../pages/Admin/AddWordToOthers").default;
					return (
						<Layout {...props}>
							<Component {...props} />
						</Layout>
					);
				}}/>
		</Switch>
	);
}
