import React from "react";
import {Link} from 'react-router-dom';
import Hint from "src/components/Hint";

const PosWord = ({word}) => (
	<span className="posword">
		<Link to={`/thesaurus/${word}`}>{word}</Link>
	,</span>
);

export default class extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			advanced: false,
		};
	}

	render() {
		let {field, row} = this.props;
		/*
		 * Validate
		 */
		if (!field || !Array.isArray(row[field])) {
			return null;
		}
		/*
		 * Predefined lists of words
		 * info = [ 0-1 (bad-ok), 0-1 (ok-proper), 0-1 (ok-unknown) ]
		 * ok = [ 1, 0, 0 ]
		 */
		let ListOk = row[field].map(word => {
			return (<PosWord word={word} key={word}/>);
		}).filter(val => !!val);
		/*
		 * Render simple version:
		 */
		if (!this.state.advanced) {
			return (
				<div className={"ui-form-section simple"}>
					<div>
						{ListOk.length > 0 && <>
							<div className={"one"}><b>{field}: </b></div>
							<div className="two fieldWords">{ListOk}</div>
						</>}
						<div
							className={"three"}
							onClick={() => {
								this.setState({advanced: true});
							}}
						>
							<i className={"fas fa-ellipsis-h"}></i>
						</div>
					</div>
				</div>
			);
		} else {
			/*
			 * Render advanced version:
			 */
			return (
				<div className={"ui-form-section advanced"}>
					<p className={"one"}>
						<b className="title">
							{field}:
						</b>
					</p>

					<div className={"two"}>
						{ListOk.length > 0 &&
						<p className="fieldWords">{ListOk}</p>
						}
					</div>

					<p
						className={"three"}
						onClick={() => {
							this.setState({advanced: false});
						}}
					>
						<i className={"fas fa-angle-up"}></i>
					</p>
				</div>
			);
		}
	}
}
