import { data_word_get } from 'src/redux/actions/api/data_word';
import { string_chunks } from 'src/redux/actions/api/suggestions';

/*
 * words
 */
export function RX__word_chunks(string = '') {
  return async dispatch => {
    let word_chunks = [];

    // chunk input (on back-end)
    let chunks = await dispatch(string_chunks(string));
    if (chunks) {
      word_chunks = chunks;
    }

    // save chunks
    dispatch({
      type: 'RX__word_chunks',
      data: word_chunks
    });

    // save input string only when got chunks
    dispatch({
      type: 'RX__word_input',
      data: string
    });
  };
}

export function RX__word_chunks_update(string = '') {
  return async (dispatch, getState) => {
    let { word } = getState();
    /*
     * Title of this function is obsolete/misleading.
     * Now, updating only the first item of first list in chunks.
     * Should be called ROW_UPDATE
     */
    if (word.chunks[0] && word.chunks[0][0] && word.chunks[0][0].key) {
      let row = await dispatch(data_word_get(word.chunks[0][0].key));
      if (row && row.key) {
        word.chunks[0][0] = row;
      }
    }
    /*
     * Save chunks
     */
    dispatch({
      type: 'RX__word_chunks',
      data: word.chunks
    });
  };
}