/**
 Throttle
 https://css-tricks.com/the-difference-between-throttling-and-debouncing/
 * @param func {function}
 * @param wait {number} - func will be allowed maximum one time during this many milliseconds
 * @param options {object} - options.leading and options.trailing - see "NPM Underscore" _.throttle
 */
export const throttle = function (func, wait, options) {
	let timeout, context, args, result;
	let previous = 0;
	if (!options) options = {};

	let later = function later() {
		previous = options.leading === false ? 0 : Date.now();
		timeout = null;
		result = func.apply(context, args);
		if (!timeout) context = args = null;
	};

	let throttled = function throttled() {
		let now = Date.now();

		if (!previous && options.leading === false) previous = now;
		let remaining = wait - (now - previous);
		context = this;
		args = arguments;

		if (remaining <= 0 || remaining > wait) {
			if (timeout) {
				clearTimeout(timeout);
				timeout = null;
			}

			previous = now;
			result = func.apply(context, args);
			if (!timeout) context = args = null;
		} else if (!timeout && options.trailing !== false) {
			timeout = setTimeout(later, remaining);
		}

		return result;
	};

	throttled.cancel = function () {
		clearTimeout(timeout);
		previous = 0;
		timeout = context = args = null;
	};

	return throttled;
};

/**
 * GET request
 * @param {string} url
 * @param {object} data
 * @returns {Promise}
 */
export const apiGET = async function (url = ``, data = {}) {
	// Auth
	// url = url;
	// Default options are marked with *
	return fetch(url, {
		method: "GET", // *GET, POST, PUT, DELETE, etc.
		mode: "cors", // no-cors, cors, *same-origin
		cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
		credentials: "same-origin", // include, *same-origin, omit
		headers: {
			"Content-Type": "application/json; charset=utf-8",
			// "Content-Type": "application/x-www-form-urlencoded",
		},
		redirect: "follow", // manual, *follow, error
		referrer: "no-referrer", // no-referrer, *client
	})
		.then(response => response.json()) // parses response to JSON
		.then(response => response.data);
};

/**
 * POST request
 * @param {string} url
 * @param {object} data
 * @returns {Promise}
 */
export const apiPOST = function (url = ``, data = {}) {
	// Auth
	// url = url;
	// Default options are marked with *
	return fetch(url, {
		method: "POST", // *GET, POST, PUT, DELETE, etc.
		mode: "cors", // no-cors, cors, *same-origin
		cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
		credentials: "same-origin", // include, *same-origin, omit
		headers: {
			"Content-Type": "application/json; charset=utf-8",
			// "Content-Type": "application/x-www-form-urlencoded",
		},
		redirect: "follow", // manual, *follow, error
		referrer: "no-referrer", // no-referrer, *client
		body: JSON.stringify(data), // body data type must match "Content-Type" header
	})
		.then(response => response.json()); // parses response to JSON
};

