import React from 'react';
import { DomainsStyled } from './Domains.styled';
import { Button, Checkbox } from 'antd';
import { asort_by_length_asc } from 'universal-common-scripts/functions/asort_strings';
import { Link } from 'react-router-dom';

const DomainsResults = function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that;
  /*
   * Same as this.render():
   */
  let {
    suggestions_all,
    suggestions_wip,
    suggestions_wip_phrases,
    suggestions_lists,
    suggestions_availability,
    tlds_user = {},
    tlds_checked = {},
    tlds_unchecked = {}
  } = that.props;
  let tlds_user_arr = Object.keys(tlds_user);
  let tlds_checked_arr = Object.keys(tlds_checked);
  let tlds_unchecked_arr = Object.keys(tlds_unchecked);

  return (
    <>
      {/*
       * TOP / CONTROLS
       */}
      <DomainsStyled className="DomainsResults head content">
        {/*
         * Results title
         */}
        {/*{!that.state.gotAvailability ? <h3>Loading domain availability... &nbsp;</h3> : <h3>Results: &nbsp;</h3>}*/}
        {/*
         * Legend
         */}
        <div className="legend">
          <span className="domname status1">
            <Checkbox
              size="small"
              checked={!that.props.hide_available}
              onChange={() => {
                that.toggleResults('available');
              }}
            />
            <span className="symbol">$</span>
            <span className="text show-large">available</span>
          </span>
          <span className="domname status2">
            <Checkbox
              size="small"
              checked={!that.props.hide_premium}
              onChange={() => {
                that.toggleResults('premium');
              }}
            />
            <span className="symbol">$$</span>
            <span className="text show-large">premium</span>
          </span>
          <span className="domname status10">
            <Checkbox
              size="small"
              checked={!that.props.hide_unavailable}
              onChange={() => {
                that.toggleResults('unavailable');
              }}
            />
            <span className="symbol hide-large">n/a</span>
            <span className="symbol show-large">not available</span>
          </span>
          <span className="domname status9">
            <Checkbox
              size="small"
              checked={!that.props.hide_other}
              onChange={() => {
                that.toggleResults('other');
              }}
            />
            <span className="symbol">?</span>
            <span className="text show-large">other</span>
          </span>
          {/*<span*/}
          {/*	className="optionsLink"*/}
          {/*	onClick={() => {*/}
          {/*	}}*/}
          {/*>*/}
          {/*	<i className={'fas fa-sliders-h'}></i>*/}
          {/*	<span>&nbsp;more filters</span>*/}
          {/*</span>*/}
        </div>

        {/*
         * Show WIP
         */}
        <span className="resultsMore" onClick={that.toggleWip}>
          <i className="fas fa-ellipsis-h"></i>
        </span>

        {/*
         * Results title
         */}
        {!that.state.gotAvailability ? <h3>Loading domain availability... &nbsp;</h3> : <h3>Results: &nbsp;</h3>}
      </DomainsStyled>
      {/*
       * BODY / CONTENT / RESULTS
       */}
      <DomainsStyled className="DomainsResults body">
        {/*
         * Columns
         */}
        <div
          className={
            'body content columns' +
            (!that.state.gotAvailability ? ' gettingAvailability' : ' gotAvailability') +
            (that.props.hide_available ? ' hideAvailable' : '') +
            (that.props.hide_unavailable ? ' hideUnavailable' : '') +
            (that.props.hide_other ? ' hideOther' : '') +
            (that.props.hide_premium ? ' hidePremium' : '')
          }
        >
          {/*
           * DOMAIN SUGGESTIONS ALL
           */}
          <div className="column domsection">
            {/*
             * Results
             */}
            <div className="domsection_content">
              {suggestions_all.map((phraseString, i) => (
                <div
                  key={i}
                  className={'domname status' + (suggestions_availability[phraseString.replace(/ /g, '')] || 0)}
                >
                  {/*<i className="far fa-heart domname-icon"></i>*/}
                  <span>{phraseString.split(' ').map((str, i) => (
                    <span key={str + i} className="word">{str}</span>
                  ))}</span>
                  &nbsp;&nbsp;
                  <span className="domname-v">v</span>
                </div>
              ))}
            </div>
          </div>

          {/*
           * TLDs
           */}
          <div className="column tlds">
            {/*<div className={'heading'}>*/}
            {/*	<div className="title">Extensions:</div>*/}
            {/*</div>*/}

            {/*
             * Buttons
             */}
            {/*<Button*/}
            {/*	className="optionsButton"*/}
            {/*	shape="round"*/}
            {/*	onClick={() => {*/}
            {/*	}}*/}
            {/*>*/}
            {/*	<i className={'fas fa-sliders-h'}></i>*/}
            {/*	<span>&nbsp;More Filters</span>*/}
            {/*</Button>*/}

            {/*<Button*/}
            {/*	className="Button"*/}
            {/*	onClick={() => {*/}
            {/*	}}*/}
            {/*>*/}
            {/*	<i className={'fas fa-plus'}></i>*/}
            {/*	<span className="hide-small">Add Extension</span>*/}
            {/*</Button>*/}

            {/*
             * User extensions
             */}
            <div className="section">
              <div className={'heading'}>
                <div className="title user">Favorite extensions:</div>
              </div>
              {!!(tlds_user_arr && tlds_user_arr.length) && (
                <>
                  {tlds_user_arr.map((tld, i) => (
                    <div key={tld + 'ch' + i} className={'tld user'}>
                      {/*<Checkbox size="small" checked={true}/>&nbsp;*/}
                      <i
                        className="fas fa-heart"
                        onClick={() => {
                          that.tld_user(tld);
                        }}
                      ></i>
                      <span>
                        <b>.</b>
                        {tld}
                      </span>
                      <i
                        className="fas fa-times"
                        onClick={() => {
                          that.tld_uncheck(tld);
                        }}
                      ></i>
                    </div>
                  ))}
                </>
              )}
              {/*
               * Add
               */}
              <div
                className={'add'}
                onClick={() => {
                  that.props.ui_actions.RX__focusSelectTld();
                }}
              >
                <i className={'fas fa-plus'}></i>
                <span>&thinsp;add</span>
              </div>
            </div>

            {/*
             * Checked
             */}
            {!!(tlds_checked_arr && tlds_checked_arr.length) && (
              <div className="section">
                <div className={'heading'}>
                  <div className="title checked">Suggested extensions:</div>
                  {/*<div className="subtitle">(click to NOT use)</div>*/}
                </div>
                {tlds_checked_arr.map((tld, i) => (
                  <div key={tld + 'ch' + i} className={'tld checked'}>
                    {/*<Checkbox size="small" checked={true}/>&nbsp;*/}
                    <i
                      className="far fa-heart"
                      onClick={() => {
                        that.tld_user(tld);
                      }}
                    ></i>
                    <span>
                      <b>.</b>
                      {tld}
                    </span>
                    <i
                      className="fas fa-times"
                      onClick={() => {
                        that.tld_uncheck(tld);
                      }}
                    ></i>
                  </div>
                ))}
              </div>
            )}

            {/*
             * Un-checked
             */}
            {!!(tlds_unchecked_arr && tlds_unchecked_arr.length) && (
              <div className="section">
                <div className={'heading'}>
                  <div className="subtitle unchecked">Not used:</div>
                  {/*<div className="subtitle">(click to use)</div>*/}
                </div>
                {tlds_unchecked_arr.map((tld, i) => (
                  <div
                    key={tld + 'unch' + i}
                    className={'tld unchecked'}
                    onClick={() => {
                      that.tld_user(tld);
                    }}
                  >
                    {/*<Checkbox size="small" checked={false}/>&nbsp;*/}
                    <i
                      className="far fa-heart"
                      onClick={() => {
                        that.tld_user(tld);
                      }}
                    ></i>
                    <span>
                      <b>.</b>
                      {tld}
                    </span>
                    <i className="fas fa-times hidden"></i>
                  </div>
                ))}
              </div>
            )}
          </div>

          {!!that.state.showWip && (
            <>
              {/*
               * DOMAIN SUGGESTIONS
               */}
              {Object.keys(suggestions_lists || {}).map((title, i) => {
                let list = suggestions_lists[title];
                return (
                  <div key={title + i} className="column">
                    <h3>
                      <b>{title}</b>
                    </h3>
                    <div className="pre">
                      {/* arr_sort_by_width(item.list)*/}
                      {list.map((phraseString, i) => {
                        return (
                          <div
                            key={i}
                            className={'domname status' + suggestions_availability[phraseString.replace(/ /g, '')] || 0}
                          >
                            {phraseString}
                          </div>
                        );
                      })}
                    </div>
                  </div>
                );
              })}

              {/*
               * WIP PHRASES (debugging, temporary)
               */}
              <div className="column">
                <div className="nowrap">
                  <b>{'wip phrases'}</b>
                </div>
                <div className="pre">{suggestions_wip_phrases.map((words, i) => JSON.stringify(words, null, ' '))}</div>
              </div>

              {/*
               * WIP PHRASE LISTS (debugging, temporary)
               */}
              {Object.keys(suggestions_wip || {}).map((title, i) => {
                let lists = suggestions_wip[title];
                return (
                  <div key={title + i} className="column">
                    <div className="nowrap">
                      <b>{title}</b>
                    </div>
                    <div className="pre">{lists.map((list, i) => JSON.stringify(list, null, ' '))}</div>
                  </div>
                );
              })}
            </>
          )}
        </div>
      </DomainsStyled>
    </>
  );
};

export default DomainsResults;
