import { apiGET } from "universal-common-scripts/functions/requests";
// import {json_parse} from "universal-common-scripts/functions/objects";


const API_HOST = process.env.REACT_APP_API_HOST;

export function string_chunks(string) {
  window.isLoading('chunks');
  return async dispatch => {
    // get word
    try {
      let results = await apiGET(API_HOST + '/api/phrase/string_chunks/' + string);
      // success
      window.doneLoading('chunks');
      return results;
    } catch (e) {
      // fail
      window.doneLoading('chunks');
      return false;
    }
  }
}
