/**
 * check last word - is it a determiner/conjunction/preposition? then remove it!
 * @params {object} phrase - { words: ['best','coffee'], tld: 'world', name: 'bestcoffee.world' }
 * @param {object} chunks_dict - object of key : {key:'',root:'',derivations:[],ok_list:[],...etc}
 * @returns phrase
 */
export default function (phrase, chunks_dict) {

	let key = phrase.words[phrase.words.length - 1];
	let row = chunks_dict[key];

	if (
		row &&
		(row.pos1 === 'determiners' || row.pos1 === 'conjunctions' || row.pos1 === 'prepositions')
	) {
		phrase.words.pop();
	}

	return phrase;
};
