import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import * as ui_actions from 'src/redux/actions/ui';
import * as domain_actions from 'src/redux/actions/domain';
import { StyledPage, StyledSearch } from '../../Layout.styled';
import Search from '../../components/Search';
import DomainsResults from './DomainsResults';
import DomainsHome from './DomainsHome';

class Domains extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tldAdd: '',
      showWip: false,
      gotAvailability: true,
    };
  }

  componentDidMount() {
    this.props.domain_actions.RX__domain_suggestions();
    // this.props.ui_actions.RX__toggle_poss();
  }

  async componentDidUpdate(prevProps) {
    /*
     * Get suggestions
     */
    // if new user input
    if (this.props.word_input !== prevProps.word_input) {
      this.props.domain_actions.RX__domain_suggestions();
    }
    // if new tld
    let prev_tlds = JSON.stringify(prevProps.tlds_user) + JSON.stringify(prevProps.tlds_checked) + JSON.stringify(prevProps.tlds_unchecked);
    let this_tlds = JSON.stringify(this.props.tlds_user) + JSON.stringify(this.props.tlds_checked) + JSON.stringify(this.props.tlds_unchecked);
    // TODO:
    //  why it didn't work with only tlds_user and tlds_checked?
    //  (why prevProps === this.props user/checked were the SAME after removed from user/checked, added to tlds_unchecked)
    //  (but prevProps.tlds_unchecked was different, as expected)
    if (prev_tlds !== this_tlds) {
      this.props.domain_actions.RX__domain_suggestions();
    }
    /*
     * Get availability
     */
    // if new suggestions
    if (this.props.suggestions_all.length && this.props.suggestions_all !== prevProps.suggestions_all) {
      this.setState({
        gotAvailability: false,
      });
      await this.props.domain_actions.RX__domain_suggestions_availability();
      this.setState({
        gotAvailability: true,
      });
    }
  }

  toggleResults = (listType) => {
    this.props.domain_actions.RX__results_toggle_list(listType);
  };
  toggleWip = () => {
    this.setState({
      showWip: !this.state.showWip
    });
  };
  tld_user = tld => {
    this.props.domain_actions.RX__tld_add(tld, 'user');
  };
  tld_check = tld => {
    this.props.domain_actions.RX__tld_add(tld, 'checked');
  };
  tld_uncheck = tld => {
    this.props.domain_actions.RX__tld_add(tld, 'unchecked');
  };

  render() {
    window.doneLoading('render');

    /*
     * Content
     */
    let PageClass = '';
    let PageContent = null;
    if (this.props.suggestions_all && this.props.suggestions_all.length) {
      PageClass = 'DomainsResults';
      PageContent = <DomainsResults that={this}/>;
    } else if (!this.props.word_chunks.length) {
      PageClass = 'DomainsHome';
      PageContent = <DomainsHome that={this}/>;
    } else {
      PageContent = <p className="content">Loading...</p>;
    }

    /*
     * View
     */
    return (
      <>
        <Search
          {...this.props}
          className={PageClass}
          domains={true}
          title="Domain Name:"
          cue={[
            <span key="1">There are now 1000+ domain extensions!&nbsp;</span>,
            <span key="2">We'll suggest the most relevant ones for you.</span>,
          ]}
        />
				<StyledPage>{PageContent}</StyledPage>
			</>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    ui_actions: bindActionCreators(ui_actions, dispatch),
    domain_actions: bindActionCreators(domain_actions, dispatch),
  };
};

const mapStateToProps = function (state) {
  return {
    // word
    word_chunks: state.word.chunks,
    word_input: state.word.input,
    // results
    hide_available: state.domain.hide_available,
    hide_unavailable: state.domain.hide_unavailable,
    hide_other: state.domain.hide_other,
    hide_premium: state.domain.hide_premium,
    // tlds
    tlds_all: state.domain.tlds_all,
    tlds_user: state.domain.tlds_user,
    tlds_checked: state.domain.tlds_checked,
    tlds_unchecked: state.domain.tlds_unchecked,
    // suggestions
    suggestions_availability: state.domain.suggestions_availability,
    suggestions_all: state.domain.suggestions_all,
    suggestions_wip: state.domain.suggestions_wip,
    suggestions_wip_phrases: state.domain.suggestions_wip_phrases,
    suggestions_lists: state.domain.suggestions_lists,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Domains));
