import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import { Styled } from './WordPoss.styled';

class WordPoss extends React.Component {
	render() {
		let { word_chunks, show_poss } = this.props;
		if (!word_chunks.length) {
			return null;
		}
		let chunks = word_chunks.flat();
		if (!chunks.length) {
			return null;
		}
		/*
		 * Render:
		 */
		return (
			<Styled className={'WordPoss' + (show_poss ? ' show' : ' hide')}>
				<section className={'columns'}>
					{chunks.map(row => {
						if (!row || !row.poss) {
							return null;
						}
						let poss = row.poss;
						let poss_keys = Object.keys(poss);
						return (
							<div key={row.key} className="column">
								"
								<b>
									<Link to={`/edit_word/${row.key}`} target="_blank">{row.key}</Link>
								</b>
								":
								<br/>
								{poss_keys.map(poss_key => {
									return (
										<section key={poss_key} className={'columns'}>
											<div className="column">
												"{poss_key}":
												<br/>
												{poss[poss_key].map ? (
													poss[poss_key].map(w => <span key={w}>{w}, &nbsp;</span>)
												) : (
													<span>{JSON.stringify(poss[poss_key], null, ' ')}</span>
												)}
											</div>
										</section>
									);
								})}
							</div>
						);
					})}
				</section>
			</Styled>
		);
	}
}

const mapDispatchToProps = dispatch => {
	return {};
};

const mapStateToProps = function (state) {
	return {
		show_poss: state.ui.show_poss,
		word_chunks: state.word.chunks
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(WordPoss));
