import React from "react";
import {Dropdown, Menu} from "antd";
import {Link} from "react-router-dom";

class Definitions extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			definitions: []
		};
	}

	async componentDidMount() {
		let defs = await this.props.api_actions.definitions_list_get(this.props.word);
		if (defs) {
			this.setState({
				definitions: defs
			});
		} else {
			this.setState({
				definitions: ['not found']
			});
		}
	}

	render() {
		if (!this.state.definitions || !Array.isArray(this.state.definitions)) {
			return null;
		}
		let List = [];
		for (let def of this.state.definitions) {
			List.push(<Menu.Item>{def}</Menu.Item>);
		}
		return (<Menu className={"antd-word-definitions-dropdown-menu"}>{List}</Menu>);
	}
}

class PosWord extends React.Component {
	render() {
		if (!this.props.row || !this.props.row.key || !this.props.word || this.props.word === ' ') {
			return null;
		}
		let menu = (
			<Menu className={"antd-edit-posword-dropdown-menu"}>
				<Menu.Item>
					<Link to={`/edit_word/${this.props.word}`}>
						<i className={"fas fa-pen"}></i> edit "<b>{this.props.word}</b>"
					</Link>
				</Menu.Item>
				<hr/>
				<Menu.Item
					className={"color-bad"}
					onClick={() => {
						this.props.api_actions.data_word_remove_words(
							this.props.row.key,
							this.props.word
						);
					}}
				>
					<i className={"fas fa-trash"}></i> remove "<b>{this.props.word}</b>"
				</Menu.Item>
				{this.props.info && this.props.info[1] === 0 && this.props.info[2] === 0 &&
				(this.props.info[0] === 0
						?
						<Menu.Item
							className={"color-good"}
							onClick={() => {
								this.props.api_actions.data_word_sentiment_of_synonym(
									this.props.row.key,
									this.props.word,
									1
								);
								this.props.api_actions.data_word_remove_words(
									this.props.row.key,
									this.props.word
								);
							}}
						>
							make positive, but remove
						</Menu.Item>
						:
						<Menu.Item
							className={"color-bad"}
							onClick={() => {
								this.props.api_actions.data_word_sentiment_of_synonym(
									this.props.row.key,
									this.props.word,
									-1
								);
								this.props.api_actions.data_word_remove_words(
									this.props.row.key,
									this.props.word
								);
							}}
						>
							mark negative, and remove
						</Menu.Item>
				)
				}
				<hr/>
				<Menu.Item
					className={"color-black"}
					onClick={() => {
						this.props.api_actions.data_word_sentiment_of_synonym(
							this.props.row.key,
							this.props.word,
							-1
						);
					}}
				>
					sentiment = negative
				</Menu.Item>
				<Menu.Item
					className={"color-good"}
					onClick={() => {
						this.props.api_actions.data_word_sentiment_of_synonym(
							this.props.row.key,
							this.props.word,
							1
						);
					}}
				>
					sentiment = ok/good
				</Menu.Item>
				<hr/>
				<Menu.Item
					className={"color-medium"}
					onClick={() => {
						this.props.api_actions.data_word_proper_of_synonym(
							this.props.row.key,
							this.props.word,
							1
						);
					}}
				>
					is Proper/Capitalized
				</Menu.Item>
				<Menu.Item
					className={"color-medium"}
					onClick={() => {
						this.props.api_actions.data_word_proper_of_synonym(
							this.props.row.key,
							this.props.word,
							0
						);
					}}
				>
					not proper
				</Menu.Item>
			</Menu>
		);
		return (
			<span>
				{/*
				 * word itself
				*/}
				<span className={"posword"}>
					<Dropdown overlay={<Definitions api_actions={this.props.api_actions} word={this.props.word}/>} trigger={['click']}>
						<span className={"posword_word1"}>
							{this.props.word.substr(0, Math.ceil(this.props.word.length * 0.3))}
						</span>
					</Dropdown>
					<Dropdown overlay={menu} trigger={['click']}>
					<span className={"posword_word2"}>
						{this.props.word.substr(Math.ceil(this.props.word.length * 0.3))}
					</span>
					</Dropdown>,
				</span>

				{/*
				 * sentiment -/+
				*/}
				<span className={"posword_sentiment"}>
					{this.props.info &&
					<span>
						{this.props.info[0] === 1 &&
						<sup
							className={"plusMinus"}
							onClick={() => {
								this.props.api_actions.data_word_sentiment_of_synonym(
									this.props.row.key,
									this.props.word,
									-1
								);
							}}
						>
							<b>&ndash;</b>
						</sup>
						}

						{(this.props.info[0] === 0 && this.props.info[1] === 0 && this.props.info[2] === 0) &&
						<sup
							className={"plusMinus"}
							onClick={() => {
								this.props.api_actions.data_word_sentiment_of_synonym(
									this.props.row.key,
									this.props.word,
									1
								);
							}}
						>
							<b>+</b>
						</sup>
						}
					</span>
					}
				</span>

				{/*
				 * delete x
				*/}
				<span className={"posword_sentiment"}>
					{this.props.info &&
					<span>
						<sup
							className={"deleteWord"}
							onClick={() => {
								this.props.api_actions.data_word_remove_words(
									this.props.row.key,
									this.props.word
								);
							}}
						>
							<b>x</b>
						</sup>
					</span>
					}
				</span>

			</span>
		);
	}
}

export default PosWord;
