import { apiGET, apiPUT } from "universal-common-scripts/functions/requests";
import { json_parse } from "universal-common-scripts/functions/objects";
import { RX__word_chunks_update } from "../word";

import { message } from "antd";

const API_HOST = process.env.REACT_APP_API_HOST;

export function data_word_get(key) {
  window.isLoading('word');
  return async dispatch => {
    // get word
    let row = await apiGET(API_HOST + '/api/data/word/' + key);
    if (row) {
      // success
      row = json_parse(row);
      window.doneLoading('word');
      // fix derivations - they need to be fixed like this systematically, but for now this is hotfix:
      if (row.derivations && !Array.isArray(row.derivations)) {
        row.derivations = [...new Set(row.derivations.split(',').map(w => w.trim()).filter(w => !!w))];
      }
      // ok
      return row;
    }
    // fail
    window.doneLoading('word');
    return false;
  }
}

export function data_word_edit(row) {
  window.isLoading('word');
  return async dispatch => {
    // save
    let success = await apiPUT(API_HOST + '/api/data/word/' + row.key, row);
    if (success) {
      // ui alert
      message.success(`edited word "${row.key}"`);
      // update data for each word
      dispatch(RX__word_chunks_update(row.key));
      window.doneLoading('word');
      return true;
    }
    window.doneLoading('word');
    return false;
  }
}

