export function data_word_add_to_others({ word, pos, addtoothers }) {
  window.isLoading('add_to_others');
  return async dispatch => {
    // save
    let success = await apiPUT(API_HOST + '/api/data/word_add_to_others', { word, pos, addtoothers });
    if (success) {
      window.doneLoading('add_to_others');
      return true;
    }
    window.doneLoading('add_to_others');
    return false;
  }
}
