import from_simple_mstring from "universal-wordsalad-scripts/suggestions/wip/mstring_simple";
import original_replace_each from "universal-wordsalad-scripts/suggestions/wip/original_replace_each";
import { str_syllables_count } from "universal-common-scripts/functions/strings";
import * as sh from "universal-common-scripts/functions/sh.js";

function add_to_wip_list(wipList, addName, addlist) {
  // validate format
  if (addlist[0] && (addlist[0][0] || addlist[1][0] || addlist[2][0] || addlist[3][0])) {
    // add list items
    wipList[addName] = wipList[addName]
      ? [...wipList[addName], ...addlist]
      : addlist;
  }
}

/**
 * Suggestions
 * @param params {object}
 * @param params.chunks_keys {array} - array of key strings
 * @param params.chunks_dict {object} - object of key : {key:'',root:'',derivations:[],ok_list:[],...etc}
 * @param params.is_tech_query {boolean} - if true, will prefer techy tlds, like ".ai"
 * @returns this {object}
 * @returns this.tlds {array} - list of domain extensions
 * @returns this.wip {object} - work in progress dict of lists of phrase lists
 * @returns this.lists {object} - work in progress dict of lists of phrase strings
 */
export default function make_lists({ chunks_keys, chunks_dict, is_tech_query }) {
  let chunks_keys_reversed = [...chunks_keys].reverse();
  /*
   *
   * MAKE .WIP LISTS
   *
   */
  /*
   * markov strings
   */
  add_to_wip_list(
    this.wip,
    'adjectives > nouns',
    [
      ...from_simple_mstring('adjectives', 'nouns', chunks_keys, chunks_dict),
      ...from_simple_mstring('adjectives', 'nouns', chunks_keys_reversed, chunks_dict)
    ]
  );
  add_to_wip_list(
    this.wip,
    'prepositions > nouns',
    [
      ...from_simple_mstring('prepositions', 'nouns', chunks_keys, chunks_dict),
      ...from_simple_mstring('prepositions', 'nouns', chunks_keys_reversed, chunks_dict)
    ]
  );
  add_to_wip_list(
    this.wip,
    'prepositions > adverbs',
    [
      ...from_simple_mstring('prepositions', 'adverbs', chunks_keys, chunks_dict),
      ...from_simple_mstring('prepositions', 'adverbs', chunks_keys_reversed, chunks_dict)
    ]
  );
  add_to_wip_list(
    this.wip,
    'verbs > adverbs',
    [
      ...from_simple_mstring('verbs', 'adverbs', chunks_keys, chunks_dict),
      ...from_simple_mstring('verbs', 'adverbs', chunks_keys_reversed, chunks_dict)
    ]
  );
  add_to_wip_list(
    this.wip,
    'verbs > nouns',
    [
      ...from_simple_mstring('verbs', 'nouns', chunks_keys, chunks_dict),
      ...from_simple_mstring('verbs', 'nouns', chunks_keys_reversed, chunks_dict)
    ]
  );
  add_to_wip_list(
    this.wip,
    'nouns > nouns',
    from_simple_mstring('nouns', 'nouns', chunks_keys, chunks_dict)
  );
  /*
   * original query phrase (replace one)
   */
  add_to_wip_list(
    this.wip,
    'replace one',
    original_replace_each(chunks_keys, chunks_dict)
  );
  /*
   * lesser attempts
   */
  add_to_wip_list(
    this.wip,
    'nouns > nounsadjectives',
    [
      ...from_simple_mstring('nouns', 'nounsadjectives', chunks_keys, chunks_dict),
      ...from_simple_mstring('nouns', 'nounsadjectives', chunks_keys_reversed, chunks_dict)
    ]
  );

  /*
   *
   * COMBINE WIP -> ONE LIST .WIP_PHRASES
   *
   */
  this.wip_phrases = []; // output
  let lists = Object.values(this.wip); // input
  let added_phrases = {};
  // loop over all lists a few times
  for (let li = 0; li < 5; li++) {
    let list_index = 0;
    /*
     * Each list
     */
    for (let list of lists) {
      // increment other lists only by 5
      list = list.slice(li * 5, li * 5 + 5);
      // phrase of list slice
      for (let words of list) {
        /*
         * Words in list
         */
        // check unique
        if (added_phrases[words.toString()]) {
          continue;
        }
        // check syllables
        let unacceptable = false;
        let words_too_long = 0;
        words.forEach(function (word, i) {
          let syls = str_syllables_count(word.substr(0, word.length - 1));
          if (syls >= 3) {
            words_too_long++;
            if (i === 0) {
              unacceptable = true;
            }
          }
        });
        if (words_too_long === words.length) {
          unacceptable = true;
        }
        if (unacceptable) {
          continue;
        }
        // save
        this.wip_phrases.push(words);
        // remember unique
        added_phrases[words.toString()] = true;
      }
      // next list
      list_index++;
    }
  }


  /*
   *
   * RETURN
   *
   */
  return this;
};
