/**
 * Sort strings by predefined preference dictionary
 * @param arr {Array.<String>} - array of strings
 * @param preferences {object} - dictionary of {string:rating}
 *      rating lt 0 == bad, gt 0 == good, if undefined, default 0
 * @param prefer_position {number} - number to multiply position, to make it more important than rating
 * @return {Array.<String>} - array of strings
 */
export default function asort_strings_by_rating_and_position(arr, preferences, prefer_position = 10) {
	let that = {};
	// that.ratings
	that.ratings = {};
	for (let string of arr) {
		// from dictionary of {string:rating}
		// good == above zero; bad == below zero;
		that.ratings[string] = preferences[string] || 0;
	}
	// prepare ratings by position
	let arr_positions = {};
	for (let i in arr) {
		let string = arr[i];
		arr_positions[string] = i;
	}
	// that.positions
	that.min_position = 0;
	that.max_position = arr.length;
	that.arr_positions = arr_positions;
	that.prefer_position = prefer_position;
	// sort
	return arr.sort(asort_strings_by_rating_and_position__helper.bind(that));
};

// helper function:
function asort_strings_by_rating_and_position__helper(a, b) {

	// higher == better
	let a_rating = this.ratings[a];
	let b_rating = this.ratings[b];

	// higher == better
	// counting position in array starting at the last item!
	// normalized to a fraction 0-1,
	// then multiplied by how much we want to weigh the position (prefer_position)
	let a_position = ((this.max_position - this.arr_positions[a]) / this.max_position) * this.prefer_position;
	let b_position = ((this.max_position - this.arr_positions[b]) / this.max_position) * this.prefer_position;

	// combine the two
	// prefer higher number
	// if b is higher, then rate it better than a
	return (b_rating + b_position) - (a_rating + a_position);
}
