import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import * as ui_actions from 'src/redux/actions/ui';
import api_actions from 'src/redux/actions/api';
import { StyledPage, StyledSearch } from '../../Layout.styled';
import Search from '../../components/Search';
import WordPoss from '../../components/WordPoss';
import ViewWordResults from './ViewWordResults';

class ViewWord extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};
		props.ui_actions.RX__search_title('Thesaurus Search:');
	}

	render() {
		window.doneLoading('render');
		let { word_chunks, word_input } = this.props;
		let PageContent = null;
		/*
		 * Verify content:
		 */
		if (!word_input || !word_chunks.length) {
			PageContent = <p>Type a word or phrase &#9757;</p>;
		} else if (!word_chunks.length || !word_chunks[0].length || !word_chunks[0][0].key) {
			PageContent = <p>Word or phrase you entered is not found &#9757;</p>;
		} else {
			PageContent = <ViewWordResults that={this}/>;
		}
		/*
		 * Render content:
		 */
		return (
			<>
				<StyledSearch className="StyledSearch">
					<Search
						{...this.props}
						domains={false}
						cue={
							<>
								<span>
									Find <b>synonyms</b> and <b>parts of speech</b>{' '}
								</span>
								<span>from our dictionary of 300,000+ words!</span>
							</>
						}
					/>
					<WordPoss {...this.props} />
				</StyledSearch>

				<StyledPage>{PageContent}</StyledPage>
			</>
		);
	}
}

const mapDispatchToProps = dispatch => {
	return {
		ui_actions: bindActionCreators(ui_actions, dispatch),
		api_actions: bindActionCreators(api_actions, dispatch)
	};
};

const mapStateToProps = function (state) {
	return {
		word_poss: state.word.poss,
		word_input: state.word.input,
		word_chunks: state.word.chunks
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ViewWord));
