// import {obj_merge} from "universal-common-scripts/functions/objects";
import { asort_by_length_and_position } from "universal-common-scripts/functions/asort_strings";
import make_wip from "universal-wordsalad-scripts/suggestions/function/wip.js";
import make_lists from "universal-wordsalad-scripts/suggestions/function/lists.js";
import make_tlds from "universal-wordsalad-scripts/suggestions/function/tlds.js";
import techterms from "../data/words/tech.js";
import * as sh from "universal-common-scripts/functions/sh.js";

/*
 * TODO:
 *
 * combine phrase suggestions
 *      original versions + word hacks + generated markov strings
 *
 * Combine each ${phrase} + ${tld}
 *
 */

/**
 * Suggestions
 * @param {array} input_chunks - array: of arrays: of objects: `{key:'',root:'',derivations:[],ok_list:[],...etc}`
 *      first array ["united states of america"]; second ["united states","of","america"]; third ["united","states"]
 * @param {array} tlds_user - favorited by user
 * @param {array} tlds - actually "tlds_checked", but dont want to change the key name.
 *      May be merged with tlds_user in future
 * @param {array} tlds_unchecked - previously checked, but unchecked by user
 * @returns {object} OP
 * @returns {array} OP.all - object - key is domain name - value is true
 * @returns {object} OP.tlds - object { tld<string>: checked<boolean> }
 * @returns {array} OP.wip_phrases - array of objects - `{title:'', list:[]}`
 * @returns {array} OP.lists - array of objects - `{title:'', list:[]}`
 *      each item of inner list property is a suggestion-phrase object - `{words:[],tld:'',index:0}`
 */
export default function (input_chunks, tlds_user = [], tlds = [], tlds_unchecked = []) {
	// console.log('list_from_chunks()');
	// console.log('tlds_user', tlds_user);
	// console.log('tlds', tlds);
	// console.log('tlds_unchecked', tlds_unchecked);
	/*
	 *
	 * OUTPUT
	 *
	 */
	let OP = {
		all: [],
		tlds_user: tlds_user,
		tlds: tlds,
		tlds_unchecked: tlds_unchecked,
		wip: {},
		wip_phrases: [],
		lists: {
			"original phrase": []
		}
	};

	/*
	 *
	 * INPUT
	 *
	 */
	let chunks = input_chunks.flat();
	let chunks_keys = chunks.map(chunk => chunk.key);
	let chunks_dict = {};
	for (let chunk of chunks) {
		chunks_dict[chunk.key] = chunk;
	}
	let is_tech_query = false;
	// each chunk
	for (let row of chunks) {
		/*
		 * is tech word ?
		 */
		if (techterms[row.key]) is_tech_query = true;
		if (row.singular && techterms[row.singular]) is_tech_query = true;
		if (row.plural && techterms[row.plural]) is_tech_query = true;
		if (row.root && techterms[row.root]) is_tech_query = true;
		if (row.acronym && techterms[row.acronym]) is_tech_query = true;
		/*
		 * promote ${row.key} to first entry in ${row.poss[pos]} list
		 */
		if (row.poss[row.pos1] && Array.isArray(row.poss[row.pos1])) {
			if (row.poss[row.pos1].includes(row.key)) {
				row.poss[row.pos1].splice(row.poss[row.pos1].indexOf(row.key), 1);
			}
			// no space
			if (!row.key.includes(" ")) {
				// add key to poss
				row.poss[row.pos1].unshift(row.key);
			}
		}
	}

	/*
	 *
	 * TLDS
	 *
	 */
	OP = make_tlds.call(OP, { chunks_keys, chunks_dict, is_tech_query });

	/*
	 *
	 * WIP 1
	 * original chunks
	 *
	 */
	OP = make_wip.call(OP, { chunks_keys, chunks_dict, is_tech_query });

	/*
	 * WIP 2
	 * ignore duplicate chunks
	 * ideal: ["petting zoo", "petting", "zoo"] - "petting zoo" and "petting" will be combined.
	 * compromise: "petting" will be ignored, because I don't know how to combine conflicting row key/values like .pos1
	 */
	let chunks_keys_r = [...chunks_keys];
	let lastWord = "";
	let fi = 0;
	while (fi < chunks_keys_r.length) {
		// new length read and compared to i each time
		let row = chunks_dict[chunks_keys_r[fi]];
		if (!row) {
			fi++;
			continue;
		}
		let key = row.key;
		// check if new word is same as old word
		let newWord = key.includes(" ") ? key.substr(0, key.indexOf(" ")) : key;
		// combine new/old ?
		if (newWord === lastWord && chunks_keys_r[fi - 1]) {
			// remove lastWord (duplicate chunk)
			chunks_keys_r.splice(fi - 1, 1); // if delete an item then don't increment index
		} else {
			fi++; // if length did not change, then increment indes like normal
		}
		// next
		lastWord = newWord;
	}
	// make wip again
	OP = make_wip.call(OP, {
		chunks_keys: chunks_keys_r,
		chunks_dict,
		is_tech_query
	});

	/*
	 *
	 * LISTS
	 *
	 */
	OP = make_lists.call(OP, { chunks_keys, chunks_dict });

	/*
	 * FLAT LIST - ALL SUGGESTIONS
	 */
	let lists = Object.values(OP.lists);
	let added_phrases = {};
	OP.all = [];
	// loop over all lists a few times
	for (let li = 0; li < 25; li++) {
		let list_index = 0;
		// each list
		for (let list of lists) {
			// increment "original phrase" by 10
			if (list_index === 0) {
				list = list.slice(li * 10, li * 10 + 10);
			} else {
				// increment other lists only by 5
				list = list.slice(li * 5, li * 5 + 5);
			}
			// phrase of list slice
			for (let phrase of list) {
				// unique?
				if (added_phrases[phrase]) {
					continue;
				}
				// add to all
				OP.all.push(phrase);
				// unique
				added_phrases[phrase] = true;
			}
			// next list
			list_index++;
		}
	}

	/*
	 * Sort all
	 */
	OP.all = asort_by_length_and_position(OP.all, 10, 4);
	// OP.all = [...new Set([...OP.lists["original phrase"], ...asort_by_length_and_position_asc(OP.all, 10)])];

	/*
	 * Return
	 */
	return OP;
}
