/**
 * Remove a word from {phraseObject} if it is the tld, or a synonym of the tld,
 * Also remove the last word(s) if it is a preposition/conjunction/determiner...
 * @params {object} phrase - { words: ['best','coffee'], tld: 'world', name: 'bestcoffee.world' }
 * @param {object} chunks_dict - object of key : {key:'',root:'',derivations:[],ok_list:[],...etc}
 * @returns phrase
 */
export default function (phrase, chunks_dict) {

  let tld = phrase.tld.toString();

  /*
   * Simple:
   * check if one of the words === [tld], then remove word
   */
  let idx_word = 0;
  while (idx_word < phrase.words.length) {
    let word = phrase.words[idx_word];
    if (
      word.toString() === tld ||
      (word + 's' === tld || word + 'es' === tld) ||
      (word === tld + 's' || word === tld + 'es')
    ) {
      // remove word from phrase
      phrase.words.splice(idx_word, 1);
      break;
    }
    idx_word++;
  }

  /*
   * Advanced:
   * check if one of the words is a synonym of tld, then remove it
   */

  // Step 1:
  // find closest word to tld (synonym)
  // by counting how many times tld occurs in each word's poss corpus
  let arr_matches_in_each_chunk = [];
  for (let key in chunks_dict) {
    let row = chunks_dict[key];
    if (!row) {
      arr_matches_in_each_chunk.push(0);
      continue;
    }
    // how many times .tld occurs in chunk text
    let text = JSON.stringify(row.poss || '');
    text = text.substr(0, text.indexOf('_') + 1 || undefined); // search synonyms until domains/before/after key
    let matches = text.split(new RegExp('"' + tld + '"', "gi")).length - 1;
    // count how many times .tld occurs in row text
    arr_matches_in_each_chunk.push([key, matches || 0]); // [ [ string, number ], ]
  }

  // Step 2:
  // compare total matches with each chunk matches, if definitive, remove word
  // total
  let num_matches_total = arr_matches_in_each_chunk.reduce((a, b) => a[1] || 0 + b[1] || 0, 0);
  // each
  for (let [key, num_tld_matches] of arr_matches_in_each_chunk) {
    // find which chunk has most synonyms of tld
    let num_other_matches = num_matches_total - num_tld_matches;
    let num_self_more = num_tld_matches - num_other_matches;
    // if definitive,
    if (num_self_more > 3) {
      // if 2+ words,
      if (phrase.words.length >= 2) {
        // found the one greatest number in array
        // remove its synonym from phrase
        let index_to_remove = phrase.words.indexOf(key);
        phrase.words.splice(index_to_remove, 1);
      }
      // add tld to phrase
      phrase.tld = tld;
      // meta
      phrase.name = phrase.words.join('') + '.' + phrase.tld;
    }
  }

  return phrase;
};
