import all_tlds from 'universal-wordsalad-scripts/data/domains/all.js';

function get_ss(key, default_value) {
	let ss_value = window.sessionStorage.getItem(key);
	// console.log('got from ss:', key, ss_value);
	return (
		ss_value
			? JSON.parse(ss_value)
			: default_value
	);
}

function set_ss(key, value) {
	window.sessionStorage.setItem(key, JSON.stringify(value));
}

/*
  1) CONFIG - DEFAULT STATE:
*/
const defState = {
	suggestions_all: [],
	suggestions_lists: [],
	suggestions_wip: [],
	suggestions_wip_phrases: [],
	suggestions_availability: get_ss('suggestions_availability', {}),
	tlds_all: all_tlds,
	tlds_user: {},//get_ss('tlds_user', {}),
	tlds_checked: {},//get_ss('tlds_checked', {}),
	tlds_unchecked: {},
	hide_available: false,
	hide_premium: false,
	hide_unavailable: false,
	hide_other: true,
};

/*
  2) REDUCE STATE:
*/
const ThisReducer = (state = defState, action) => {
	const newState = { ...state };

	/*
    3) HANDLE ACTIONS:
  */
	switch (action.type) {

		/*
		 * Results
		 */
		case 'RX__results_toggle_list':
			if (action.listType && newState.hasOwnProperty('hide_' + action.listType)) {
				newState['hide_' + action.listType] = !newState['hide_' + action.listType];
			}
			break;

		/*
		 * Suggestions
		 */
		case 'RX__domain_suggestions':
			let data = action.suggestions || {};
			newState.suggestions_all = data.all || [];
			newState.suggestions_lists = data.lists || [];
			newState.suggestions_wip = data.wip || [];
			newState.suggestions_wip_phrases = data.wip_phrases || [];
			break;

		case 'RX__domain_suggestions_availability':
			if (action.data) {
				newState.suggestions_availability = { ...newState.suggestions_availability, ...action.data };
				// persist
				set_ss('suggestions_availability', newState.suggestions_availability);
			}
			break;

		/*
		 * Tlds
		 */

		/**
		 * @action.tlds {array}
		 */
		case 'RX__tlds_checked':
			if (action.tlds) {
				// new
				let tlds_checked = {};
				for (let tld of action.tlds) {
					// skip if already in user favorites
					if (newState.tlds_user[tld]) continue;
					// remove from old
					delete newState.tlds_unchecked[tld];
					// add to new
					tlds_checked[tld] = true;
				}
				// new before old
				newState.tlds_checked = { ...tlds_checked, ...newState.tlds_checked };
				// persist
				set_ss('tlds_user', newState.tlds_user);
				set_ss('tlds_checked', newState.tlds_checked);
			}
			break;

		/**
		 * @action.tld {string}
		 */
		case 'RX__tld_user':
			if (action.tld) {
				// add
				newState.tlds_user = { ...{ [action.tld]: true }, ...newState.tlds_user };
				// remove
				delete newState.tlds_checked[action.tld];
				delete newState.tlds_unchecked[action.tld];
				// persist
				set_ss('tlds_user', newState.tlds_user);
				set_ss('tlds_checked', newState.tlds_checked);
			}
			break;

		/**
		 * @action.tld {string}
		 */
		case 'RX__tld_check':
			if (action.tld) {
				// add
				newState.tlds_checked = { ...{ [action.tld]: true }, ...newState.tlds_checked };
				// remove
				delete newState.tlds_unchecked[action.tld];
				// persist
				set_ss('tlds_user', newState.tlds_user);
				set_ss('tlds_checked', newState.tlds_checked);
			}
			break;

		/**
		 * @action.tld {string}
		 */
		case 'RX__tld_uncheck':
			// add
			newState.tlds_unchecked = { ...{ [action.tld]: true }, ...newState.tlds_unchecked };
			// remove
			delete newState.tlds_user[action.tld];
			delete newState.tlds_checked[action.tld];
			// persist
			set_ss('tlds_user', newState.tlds_user);
			set_ss('tlds_checked', newState.tlds_checked);
			break;
	}

	/*
    4) RETURN (COPY OF) STATE:
  */
	return newState;
};

export default ThisReducer;
