import React from "react";
import {Link} from 'react-router-dom';
import {Button, Dropdown, Input, Menu, Tooltip} from "antd";
import {withRouter} from 'react-router-dom';
import Tip from "src/components/Tip";
import Hint from "src/components/Hint";
import PosWord from "./PosWord";

class Pos extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			addwords: ''
		};
	}

	render() {
		let {pos, row, api_actions} = this.props;
		/*
		 * Validate
		 */
		if (!pos) {
			return null; // its ok, probably empty row.pos3
		}
		if (!row.pos_dict[pos]) {
			row.pos_dict[pos] = {}; // its ok, prepare list if not exist
		}
		let slist_pos = row.pos_dict[pos];
		/*
		 * Predefined lists of words
		 * info = [ 0-1 (bad-ok), 0-1 (ok-proper), 0-1 (ok-unknown) ]
		 * ok = [ 1, 0, 0 ]
		 */
		let ListOk = Object.entries(slist_pos).map((tuple) => {
			let [word, info] = tuple;
			if (info.toString() === '1,0,0') {
				return (
					<PosWord info={info} row={row} key={word} word={word} api_actions={api_actions}/>
				);
			}
		}).filter(val => !!val);
		if (ListOk.length > 30) {
			ListOk.splice(30, 0, <div key="break30" className="break30">---30---</div>);
		}
		let ListBad = Object.entries(slist_pos).map((tuple) => {
			let [word, info] = tuple;
			if (info.toString() === '0,0,0') {
				return (
					<PosWord info={info} row={row} key={word} word={word} api_actions={api_actions}/>
				);
			}
		}).filter(val => !!val);
		let ListProper = Object.entries(slist_pos).map((tuple) => {
			let [word, info] = tuple;
			if (info[1] === 1) {
				return (
					<PosWord info={info} row={row} key={word} word={word} api_actions={api_actions}/>
				);
			}
		}).filter(val => !!val);
		let ListUnknown = Object.entries(slist_pos).map((tuple) => {
			let [word, info] = tuple;
			if (info[2] === 1) {
				return (
					<PosWord info={info} row={row} key={word} word={word} api_actions={api_actions}/>
				);
			}
		}).filter(val => !!val);
		/*
		 * Render:
		 */
		return (
			<div className={"Pos ui-form-section"}>
				<div className={"posDiv noselect"}>
					<Tooltip title={
						<div className={"help"}>
							<p>To remove this section, or move up/down, edit "POS" list above!</p>
							<p>
								Add synonyms to beginning of list.
							</p>
							<p>
								They will be added only to the "OK/Good" list. If you must add "Bad" (negative) words, contact the developer.
							</p>
						</div>
					}>
						<b className="title">
							{pos}
							{/*{row.pos_sentiment[pos] < 40 ? <>&#128545;</> : <>&#128512;</>}&thinsp;*/}
							:
						</b>
						<Tip/>
					</Tooltip>
				</div>
				<div className={"flexrow posDiv"}>
					<span className={"flexgrow ui-field-input-group"}>
						<Input
							type={"text"}
							placeholder={"comma, separated, words, to add to OK terms..."}
							value={this.state.addwords}
							onChange={(event) => {
								this.setState({
									addwords: event.target.value
								});
							}}
							onKeyPress={(event) => {
								if (event.key === "") {
									this.props.api_actions.data_word_remove_words(row.key, this.state.addwords);
									this.setState({addwords: ''});
									setTimeout(() => {
										this.setState({addwords: ''});
									}, 100);
								}
								if (event.key === "Enter") {
									this.props.api_actions.data_word_add_poswords(row.key, pos, this.state.addwords);
									this.setState({addwords: ''});
								}
							}}
						/>
						<Button
							type={"primary"}
							onClick={() => {
								this.props.api_actions.data_word_add_poswords(row.key, pos, this.state.addwords);
								this.setState({addwords: ''});
							}}
						>
							<i className={"fas fa-pen"}></i>
							<b>&nbsp;+&nbsp;</b>
							<i className={"fas fa-save"}></i>
						</Button>
					</span>
					&nbsp;
					{/*<Dropdown*/}
					{/*	overlay={(*/}
					{/*		<Menu className={"antd-edit-posword-dropdown-menu"}>*/}
					{/*			<Menu.Item*/}
					{/*				className={"color-bad"}*/}
					{/*				onClick={() => {*/}
					{/*					this.props.api_actions.data_word_remove_words(row.key, this.state.addwords);*/}
					{/*					this.setState({addwords: ''});*/}
					{/*				}}*/}
					{/*			>*/}
					{/*				<i className={"fas fa-trash"}></i> delete words*/}
					{/*			</Menu.Item>*/}
					{/*		</Menu>*/}
					{/*	)}*/}
					{/*	trigger={['click']}*/}
					{/*>*/}
					{/*	<Button>*/}
					{/*		<i className={"fas fa-caret-down"}></i>*/}
					{/*	</Button>*/}
					{/*</Dropdown>*/}
					<Button
						className={"color-bad"}
						onClick={() => {
							this.props.api_actions.data_word_remove_words(row.key, this.state.addwords);
							this.setState({addwords: ''});
						}}
					>
						<i className={"fas fa-trash"}></i>
					</Button>
				</div>

				{ListOk.length > 0 &&
				<div className={"posDiv"}>
					{/*<sub>{<>&#128512;</>}: </sub>*/}
					<Hint className={"color-good opacity50 noselect"}>OK/Good:</Hint>
					<div className="posWords">{ListOk}</div>
				</div>
				}
				{ListBad.length > 0 &&
				<div className={"posDiv"}>
					{/*<sub>{<>&#128545;</>}: </sub>*/}
					<Hint className={"color-bad opacity50 noselect"}>Negative:</Hint>
					<div className="posWords">{ListBad}</div>
				</div>
				}
				{ListProper.length > 0 &&
				<div className={"posDiv"}>
					{/*<sub>{<>&#127963;</>}: </sub>*/}
					<Hint className={"color-medium opacity75 noselect"}>Proper:</Hint>
					<div className="posWords">{ListProper}</div>
				</div>
				}
				{ListUnknown.length > 0 &&
				<div className={"posDiv"}>
					{/*<sub>{<>&#10067;</>}: </sub>*/}
					<Hint className={"color-medium opacity75 noselect"}>Unknown sentiment:</Hint>
					<div className="posWords">{ListUnknown}</div>
				</div>
				}
			</div>
		);
	}
}

export default withRouter(Pos);
