import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import Layout from 'src/Layout';

export default function (props) {
  return (
    <Switch>
			<Redirect from="/domains/:key" to="/domain/:key"/>
      <Redirect from="/domains" to="/domain"/>
      <Route
        key="domain"
        path="/domain/:key?"
        render={props => {
          let Component = require('src/pages/Domains').default;
          return (
            <Layout {...props}>
							<Component {...props} />
						</Layout>
          );
        }}
      />
			<Route
        key="thesaurus"
        path="/thesaurus/:key?"
        render={props => {
          let Component = require('src/pages/ViewWord').default;
          return (
            <Layout {...props}>
							<Component {...props} />
						</Layout>
          );
        }}
      />
		</Switch>
  );
}
