/**
 * Replace each word in a phrase, X times
 *        X = 2/3 of the number of 1st POS synonyms of that word
 *        `Math.floor(row.poss[row.pos1].length * 0.67)`
 * @param chunks_keys {array} - array of key strings
 * @param chunks_dict {object} - object of key : {key:'',root:'',derivations:[],ok_list:[],...etc}
 * @returns {array} array - array of arrays (each child array value is a word)
 */
export default function (chunks_keys, chunks_dict) {
	let original_phrase_arr = [...chunks_keys];
	let lists_of_phrases = [];

	/*
	 * Iterate each chunk
	 */
	for (let chunk_i = 0; chunk_i < original_phrase_arr.length; chunk_i++) {
		let phrases = [];
		/*
		 * Chunk row
		 */
		let key = original_phrase_arr[chunk_i];
		let row = chunks_dict[key];
		if (!row || !row.poss || !row.list_count) {
			original_phrase_arr.splice(chunk_i, 1);
			continue;
		}
		let row_syns = row.poss[row.pos1] || [];
		/*
		 * How many synonyms to use
		 */
		for (let syn_i = 0; syn_i <= Math.floor(row_syns.length * 0.67); syn_i++) {
			let syn = row_syns[syn_i];
			/*
			 * Add phrase with replaced word
			 */
			let new_phrase = [...original_phrase_arr];
			new_phrase.splice(chunk_i, 1, syn);
			phrases.push(new_phrase);
		}
		/*
		 * Add phrases to lists_of_phrases
		 */
		lists_of_phrases.push(phrases);
	}

	return lists_of_phrases.flat();
};

