/*
  1) CONFIG - DEFAULT STATE:
*/
const defaultState = {
	input: '', // simple string, user-entered
	chunks: [], // contains objects: { key: '', row: {} } - user input string, parsed into possible DB word rows
};

/*
  2) REDUCE STATE:
*/
const ThisReducer = (state = defaultState, action) => {
	const newState = {...state};

	/*
    3) HANDLE ACTIONS:
  */
	switch (action.type) {
		/*
		 * Input (query)
		 */
		case 'RX__word_input':
			newState.input = action.data || defaultState.input;
			break;

		case 'RX__word_chunks':
			newState.chunks = action.data || defaultState.chunks;
			break;
	}
	/*
		4) RETURN (COPY OF) STATE:
	*/
	return newState;
};

export default ThisReducer;
